.formContainer {
    display: flex;
    row-gap: 24px;
    flex-direction: column;
    align-items: center;
    margin: 0 32px;
}

.formButton {
    width: 100px;
    height: 32px;
    border-radius: 8px;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
        rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
        rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    border-color: transparent;
}